html, body, #root {
  height: 100%;
}

p {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
  padding: 10px;
  overflow: auto;
  height: 100%;
  box-sizing: border-box;
}
.home::-webkit-scrollbar {
  width: 10px;
}
.home::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
.home::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

.module-holder {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.module-holder:last-of-type {
  margin-bottom: 0;
}
.module-title {
  min-width: 80px;
  text-align: center
}


.block {
  padding: 15px;
}
.block-row {
  display: flex;
  align-items: center;
  margin: 7px 0;
}
.block-row-label {
  margin: 0;
  min-width: 150px;
}
.block-row-input {
  height: 30px;
}


.module {
  padding: 15px;
  box-sizing: border-box;
  height: 100%;
  overflow-y: auto;
}
.module-header-title {
  margin-top: 0;
}
.module-table {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto;
  background-color: pink;
  grid-gap: 1px;
  padding: 3px;
}
.grid-cell {
  background-color: #fff;
}
.sidebar {
  display: flex;
  flex-direction: column;
}

.sidebar-element,
.sidebar-accardion-title {
  display: block;
  text-decoration: none;
  color: black;
  padding: 15px;
  cursor: pointer;
}

.sidebar-accardion {
  max-height: 0;
  -webkit-transition: max-height 1s;
  transition: max-height 1s;
  /*border: 1px solid black;*/
  overflow: auto;
}
.sidebar-accardion.opened {
  max-height: 300px;
}

.sidebar-accardion .sidebar-element {
  padding-left: 30px;
}

.sidebar-element.selected {
  background-color: rgba(255,255,255,.4);
}
.app {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header {
  height: 60px;
  background-color: pink;
}

.main {
  height: 100%;
  display: flex;
}

.sidebar {
  width: 40%;
  height: 100%;
  max-width: 300px;
  background-color: green;
}
.content {
  width: 100%;
  height: 100%;
  background-color: #ddd;
}

.module-block {
    background-color: gray;
    min-height: 100px;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: auto auto auto auto auto auto auto auto;
    padding: 10px;
    width: 100%;
}
.module-normal {
    background-color: rgba(0,255,0,.4);
}
.module-error {
    background-color: rgba(255,0,0,.3);
}


.module-sensor {
    min-width: 30px;
    height: 30px;
    background-color: rgba(0,0,0,.4);
    color: white;
    position: relative;
    cursor: pointer;
}
.module-sensor-normal {
    background-color: green;
}
.module-sensor-error {
    background-color: rgba(255,0,0,.7);
}

.module-sensor-id {
    position: absolute;
    top: 2px;
    right: 2px;
    font-size: 10px;
    color: white;
}
.module-sensor-info {
    text-align: center;
    line-height: 30px;
}
.tooltip {
    position: absolute;
    min-width: 130px;
    visibility: hidden;
    top: -55px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 100;
}
.tooltip.visible {
    visibility: visible;
}

.tooltip-content {
    height: 45px;
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
    border-radius: 5px;
    background: white;
    color: black;
    box-shadow: 0 2px 20px 3px rgba(0,0,0,.5);
}
.tooltip-row {
    margin: 0;
    font-size: 12px;
}
.tooltip-arrow {
    width: 0;
    height: 0;
    margin: 0 auto;
    border-top: 10px solid white;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}
